___CSS_LOADER_EXPORT___.push([module.id, `.todo-list .detail {
  flex: 3;
  border: 1px solid var(--border-color);
  border-radius: 5px;
}

.todo-list .detail.hidden {
  display: none;
}

.todo-list .detail .head {
  padding: var(--section-padding);
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.todo-list .detail .body {
  padding: var(--section-padding);
  display: grid;
  grid-template-columns: 1fr 70px;
  grid-template-rows: auto 1fr;
  align-items: start;
}
.todo-list .detail .body .actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
.todo-list .detail .body .description {
  color: var(--sub-text-color);
}
.todo-list .detail .body .due-date {
  font-size: 0.9rem;
}

.todo-list .detail .checklist {
  display: flex;
  flex-direction: column;
  gap: 7px;
  margin-top: 10px;
  grid-column: 1 / -1;
}
.todo-list .detail .checklist-item {
  border-radius: 5px;
  border: 1px solid var(--border-color);
  transition: all ease 0.1s;
}
.todo-list .detail .checklist-item:hover {
  background-color: var(--overlay-color);
}
.todo-list .detail .checklist-item input {
  cursor: pointer;
}
.todo-list .detail .checklist-item label {
  display: flex;
  align-items: center;
  gap: 7px;
  padding: var(--section-padding);
  flex: 1;
  cursor: pointer;
}
.todo-list .detail .checklist-item:has(input:checked) {
  background-color: var(--overlay-color);
}
.todo-list .detail .checklist-item label:has(input:checked) {
  text-decoration: line-through;
}
