___CSS_LOADER_EXPORT___.push([module.id, `.btn {
  font-size: 0.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7px;
  border-radius: 5px;
  padding: 7px 12px;
  font-weight: 500;
  transition: all ease 0.1s;
}
.btn:active {
  transform: scale(0.9);
}

.btn-square {
  width: 30px;
  height: 30px;
  padding: 7px;
}

.btn-default {
  background-color: var(--overlay-color);
}

.btn-default:hover {
  background-color: #eeeeee;
}

.btn-primary {
  color: #fff;
  background-color: var(--primary-color);
}

.btn-discard {
  color: #fff;
  background-color: var(--red);
}
