___CSS_LOADER_EXPORT___.push([module.id, `@keyframes zoomOut {
  0% {
    opacity: 0.8;
    transform: scale(1);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--modal-background-color);
  animation: fadeIn 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal .container {
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: var(--background-color);
  animation: zoomOut 0.4s ease-in-out;
  border-radius: 5px;
}
.modal .container .head {
  padding: var(--section-padding);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
}
.modal .container .body {
  padding: var(--section-padding);
  overflow-y: auto;
  flex: 1;
}
.modal .container .foot {
  padding: var(--section-padding);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
}
